export default {
  components: {},

  data() {
    return {
      isOver: false,
      isClick: false
    }
  },
  computed: {
    randomColor() {
      return '#' + Math.floor(Math.random() * 16777215).toString(16)
    }
  },
  methods: {
    onStart() {
      this.isOver = true
      this.isClick = true
    },
    onEnd() {
      this.isOver = false
      if (this.isClick) {
        this.$emit('successClick')
      }
    },
    onMove() {
      this.isClick = false
    }
  }
}
