import { List } from 'vant'
import card from '@/components/card'
import vFooter from '@/components/footer'
export default {
  components: {
    card,
    vFooter,
    [List.name]: List
  },

  data() {
    return {
      loading: false,
      finished: false,
      projectList: [
        // {
        //   id: '1',
        //   title: 'Pomellato Adv 2019',
        //   author: 'Bodha&Gilda.',
        //   cover: 'images/background/IMG_0769.jpg'
        // },
        // {
        //   id: '2',
        //   title: 'Pomellato Adv 2019',
        //   author: 'Bodha&Gilda.',
        //   cover: 'images/background/IMG_0766.jpg'
        // },
        // {
        //   id: '3',
        //   title: 'Pomellato Adv 2019',
        //   author: 'Bodha&Gilda.',
        //   cover: 'images/background/IMG_0781.png'
        // },
        // {
        //   id: '4',
        //   title: 'Pomellato Adv 2019',
        //   author: 'Bodha&Gilda.',
        //   cover: 'images/background/IMG_0792.png'
        // },
        // {
        //   id: '5',
        //   title: 'Pomellato Adv 2019',
        //   author: 'Bodha&Gilda.',
        //   cover: 'images/background/IMG_0798.png'
        // }
      ],
      page: {
        number: 0,
        size: 10,
        total: 1
      }
    }
  },

  methods: {
    onLoadProject() {
      this.page.number++
      this.reFindRecommendProjectList()
      // 加载状态结束
      this.loading = false
    },
    reFindRecommendProjectList() {
      this.$callApi({
        url: 'api/mobile/project/recomList',
        method: 'post',
        data: {
          pageSize: this.page.size,
          pageNumber: this.page.number
        }
      }).then(data => {
        console.log('当前数据', data.page)
        this.projectList = this.projectList.concat(data.page.list)
        this.page.total = data.page.totalRow
        console.log(this.projectList.length + '-------' + this.page.total)

        if (this.projectList.length === this.page.total) {
          console.log('进入条件')

          this.finished = true
        }
      })
    }
  }
}
